.App {
  background-color: #000000;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  width: 100vw;
  max-width: 700px;
  height: 100vh;
  padding: 5px;
}

.Avatar {
  height: 100%;
  width: 100%;
  pointer-events: none;
}

.App-header {
  background-color: #000000;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: #f7bf06;
  width: 100%;
  height: 22%;
  padding: 2px;
  border: 2px solid #f7bf06;
}

.App-body {
  background-color: #000000;
  color: #f7bf06;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  width: 100%;
  height: 60%;
  padding: 2px;
  line-height: 0;
  border: 2px solid #f7bf06;
}

.App-footer {
  width: 100%;
  height: 8%;
}

.header-left {
  width: 55%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #f7bf06;
}

.headl1 {
  width: 50%;
  height: 60%;
  padding: 0px;
  border: 1px solid #f7bf06;
}

.headl2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50%;
  height: 60%;
  border: 1px solid #f7bf06;
}

.headl21 {
  width: 100%;
  height: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2vh;
  padding: 0px;
}

.headl22 {
  width: 100%;
  height: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: 6vh;
  color: #73ff54;
  padding: 0px;
}

.headl3 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 40%;
  border: 1px solid #f7bf06;
}

.headl31 {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60%;
  height: 100%;
  font-size: 3vh;
}

.headl32 {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40%;
  height: 100%;
  font-size: 6vh;
  color: #73ff54;
}

.header-right {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 45%;
  height: 100%;
  border: 1px solid #f7bf06;
}

.headr1 {
  width: 100%;
  height: 15%;
  font-size: 2vh;
  text-align: center;
  padding: 0px;
}

.headr2 {
  width: 100%;
  height: 80%;
  padding: 2px;
}

.grid-item {
  padding: 2px;
  border: 1px solid #f7bf06;
}

.button {
  background-color: #f7bf06;
  border: none;
  color: rgb(0, 0, 0);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: 6vh;
  height: 100%;
  width: 100%;
}
